.bg__gray-lightest { background-color: $color__gray-lightest; }
.bg__white { background-color: $color__white; }

.bg__purple { background-color: $color__purple; }
.bg__blurple { background-color: $color__blurple; }
.bg__blue { background-color: $color__blue; }
.bg__pink { background-color: $color__pink; }
